import React, {useState} from 'react';
import {ToolItem} from './ToolItem';
import {FiPlusCircle} from 'react-icons/fi/index';
import {cleanBorder, primary15, primary4, primary45} from '../utils/colors';
import UserItem from './UserItem';
import useTextInput from '../hooks/useTextInput';
import {capitalize} from '../utils/Utils';

const styles = {
  adminTools: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  messageText: {
    color: primary45,
    textAlign: 'center',
    marginBottom: 4,
  },
  panel: {
    borderRadius: 4,
    marginBottom: 8,
    border: cleanBorder,
    boxSizing: 'border-box',
  },
  panelTitle: {
    display: 'flex',
    justifyContent: 'space-between',
    fontSize: 15,
    lineHeight: '15px',
    marginBottom: 4,
    textAlign: 'center',
    backgroundColor: primary15,
    color: primary4,
    padding: 4,
    height: 20,
  },
  panelBody: {
    padding: 6,
  },
  third: {
    width: '33.3%',
    display: 'flex',
    alignItems: 'center',
    textAlign: 'center',
    justifyContent: 'center',
  },
};
export default function AccessDialogPanel({
  type,
  admins,
  addUser,
  users,
  myID,
  message,
}) {
  const [addMode, setAddMode] = useState(false);

  const AddUserInput = useTextInput(
    addMode,
    async (userID) => {
      await addUser(userID);
      setAddMode(false);
    },
    () => setAddMode(false),
    '',
    {
      placeholder: 'user id',
      confirmTitle: `Add ${type}`,
    },
  );

  return (
    <div style={styles.panel}>
      <div style={styles.panelTitle}>
        {!addMode ? (
          <>
            <div style={styles.third} />
            <div style={styles.third}>{capitalize(type)}s</div>
          </>
        ) : null}
        <div
          style={{
            ...styles.adminTools,
            ...styles.third,
            justifyContent: 'flex-end',
            width: addMode ? '100%' : '30%',
          }}>
          {!addMode && admins.includes(myID) && addUser ? (
            <>
              <ToolItem
                iconComponent={FiPlusCircle}
                title={`Add ${type}`}
                changeColor={primary4}
                defaultColor={primary4}
                onClick={() => setAddMode(true)}
              />
            </>
          ) : null}
          {addMode ? AddUserInput : null}
        </div>
      </div>
      <div style={styles.panelBody}>
        <div style={styles.users}>
          {users?.length === 0 ? (
            <div style={styles.messageText}>{message}</div>
          ) : users === null ? (
            <div style={styles.messageText}>Loading...</div>
          ) : (
            <div>
              {users.map((user) => (
                <UserItem key={user} pubKey={user} myID={myID} />
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
